import React from "react";
import Button from "@mui/material/Button";

function Card(props) {
  return (
    <div className="Card">
      <h3>{props.title}</h3>
      <p>{props.text}</p>
      {props.link && (
        <Button variant="outlined" href={props.link}>
          Check it out
        </Button>
      )}
    </div>
  );
}

export default Card;
