import "../styles.css";
import Header from "./Header";
import Card from "./Card";
import Hero from "./Hero";
import Footer from "./Footer";
import experience from "./experience";
import skills from "./skills";
import projects from "./projects";

export default function App() {
  const cardList = (card, index) => {
    return (
      <Card
        key={index}
        id={index}
        title={card.title}
        text={card.text}
        link={card.link}
      />
    );
  };

  return (
    <div className="App">
      <Header />
      <main>
        <Hero id="hero" />

        <section id="skills">
          <h2>Skills</h2>
          {skills.map(cardList)}
        </section>
        <section id="experience">
          <h2>Experience</h2>
          {experience.map(cardList)}
        </section>
        <section id="projects">
          <h2>Projects</h2>
          {projects.map(cardList)}
        </section>
      </main>
      <Footer />
    </div>
  );
}
